import { calculateHowMuchIsLeft } from 'widget/Schedule/helpers/schedule'
import { Directions } from 'widget/Schedule/types/Stops'
import { ComplainType } from '../model/Complains'

export const getMinutesString = (amount: number, secondWord = '') => {
	if (amount === 0) return ' '
	if (amount === 1) return `  ${secondWord}`
	if ([2, 3, 4].includes(amount)) return `  ${secondWord}`

	return `  ${secondWord}`
}

export const getDirectionString = (direction: Directions) => {
	return direction === 'in' ? ' ' : ' '
}

export const getTypeString = (type: ComplainType) => {
	switch (type) {
		case ComplainType.earlier:
			return ' '
		case ComplainType.later:
			return ' '
		case ComplainType.not_arrive:
			return ' '
		default:
			return ''
	}
}

export const getOnString = (on: number, type: ComplainType) => {
	if (on === 0) return getTypeString(type)
	return `${getTypeString(type)}  ${on}${getMinutesString(on, '')}`
}

export const getHumanDate = (date: string) => {
	const time = calculateHowMuchIsLeft(date)
	const d = new Date(date)
	const min = d.getMinutes() < 9 ? `0${d.getMinutes()}` : d.getMinutes()
	if (!time || time.hours === null || time.hours >= 1) return ` ${d.getHours()}:${min}`

	if (time.minutes === null) return

	return `${time.minutes === 0 ? '' : time.minutes}${getMinutesString(time.minutes)}`
}
